/* Dark overlay behind the modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); /* semi-transparent background */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

/* Modal box */
.modal-content {
  background: #fff;
  padding: 25px;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
  position: relative;
  animation: fadeIn 0.3s ease-in-out;
}

/* Close button */
.modal-close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 22px;
  background: none;
  border: none;
  cursor: pointer;
  color: #666;
  transition: 0.2s ease;
}

.modal-close:hover {
  color: #e63946;
}

/* Payment option buttons */
.payment-option-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  background: #f8f9fa;
  border: 2px solid #ddd;
  border-radius: 10px;
  padding: 12px;
  margin: 10px 0;
  width: 100%;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.payment-option-button:hover {
  background: #ffe8d6;
  border-color: #ff7b00;
}

/* Button images */
.payment-option-button img {
  width: 32px;
  height: 32px;
  object-fit: contain;
}

/* Small fade-in animation */
@keyframes fadeIn {
  from {
    transform: scale(0.9);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.payment-option-button {
  border: none;
  margin: 10px;
}
